*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.bar{
    margin: 20px;
    display: flex;
    .image{
        margin-right: 30px;
        width: 50px;
        height: 50px;
        .img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            animation-name: imgRotate;
        }
    }
}